* {
    -moz-transition: all .5s;
    -o-transition: all .5s;
    -webkit-transition: all .5s;
    transition: all .5s;
}

*[draggable=true] nav
,*[draggable=true] a
,*[draggable=true] legend
,*[draggable=true] label {
    cursor: move;
}

*[draggable=true] nav:hover
,*[draggable=true] legend:hover
,*[draggable=true] label:hover {
    background-color: #cce5ff !important;
}

div._loading_overlay_wrapper {
    position: unset;
}

div.form-group {
    margin-bottom: 0;
}

/* 点滅 */
a.blinking
,button.blinking {
	-webkit-animation:blink .7s ease-in-out infinite alternate;
    -moz-animation:blink .7s ease-in-out infinite alternate;
    animation:blink .7s ease-in-out infinite alternate;
    color: red;
}
@-webkit-keyframes blink {
    0% {opacity:0;}
    100% {opacity:1;}
}
@-moz-keyframes blink {
    0% {opacity:0;}
    100% {opacity:1;}
}
@keyframes blink {
    0% {opacity:0;}
    100% {opacity:1;}
}

